body {
 background-color: rgb(161, 102, 255);
}
.grid-container {
    padding: .5vh;
    display: grid;
    grid-template-columns: 16vh 16vh 16vh;
    grid-template-rows: 16vh 16vh 25.5vh; 
    height: 50vh;
    justify-content: center;
    align-content: center;
    justify-items: center;
    align-items: center;
}

.symbol {
    z-index: 3;
    width: 100%;
    height: auto;
    filter: brightness(0%);
    cursor: pointer;
}

.symbol:hover {
    width: 105%;
}

.centerSymbol {
    display: flex;
    grid-area:2/2;
    z-index: 3;
    width: 100%;
    height: auto;
    cursor: default;
    justify-content: center;
    align-content: center;
    opacity: 80%;
    filter: drop-shadow(.05vh .1vh 1vh rgba(255, 164, 111, 0.541));
    animation: dance 4s infinite;
}

.clover {
    z-index: 3;
    grid-area:1/1;
    width: 70%;
    height: auto;
    filter: drop-shadow(1vh 1vh .5vh #3430617e);
    animation: dance 7s infinite;
}

.cat {
    z-index: 3;
    grid-area:1/3;
    width: 70%;
    height: auto;
    filter: drop-shadow(1.3vh 1vh .5vh #3430617e);
    animation: dance 6.5s infinite;
}

.horseshoe {
    z-index: 3;
    grid-area:3/2;
    width: 70%;
    height: auto;
    filter: drop-shadow(1.15vh 1.4vh .5vh #3430617e);
    animation: dance 6s infinite;
}

@keyframes dance {
    0% {
        transform:rotate(0.0) translate(0px, 0%);
    }
    20% {
        transform:rotate(0.003turn) translate(1px, 1%);
    }
    50% {
        transform:rotate(-0.005turn) translate(1px, -1%);
    }
    77% {
        transform:rotate(0.004turn) translate(-1px, 1%);
    }
    100% {
        transform:rotate(0.0) translate(0px, 0%);
    }
}

#color1 {
    filter: brightness(9%);
    animation: bright1 3.5s ease-out;
}
#color2 {
    filter: brightness(5%);
    animation: bright2 3.5s ease-out;
}
@keyframes bright1 {
    0% {
        filter: brightness(0%);
    }
    7% {
        filter: brightness(100%);
    }
    40% {
        filter: brightness(110%);
    }
    50% {
        filter: brightness(107%);
    }
    70% {
        filter: brightness(110%);
    }
    100% {
        filter: brightness(0%);
    }
    
}
@keyframes bright2 {
    0% {
        filter: brightness(0%);
    }
    6% {
        filter: brightness(103%);
    }
    40% {
        filter: brightness(107%);
    }
    50% {
        filter: brightness(113%);
    }
    70% {
        filter: brightness(107%);
    }
    100% {
        filter: brightness(0%);
    }
    
}


#AnswerHidden {
    z-index: 3;
    justify-self: center;
    align-self: center;
    width: 0%;
    height: auto;
    cursor: default;
    filter: brightness(0%);
}
#AnswerTrue {
    justify-self: center;
    align-self: center;
    width: 40%;
    cursor: default;
    filter: blur(30px) brightness(0%);
    opacity: 0%;
    animation: revealtrue 3s ease-in;
}
#AnswerFalse {
    justify-self: center;
    align-self: center;
    width: 40%;
    cursor: default;
    filter: blur(30px) brightness(0%);
    opacity: 0%;
    animation: revealfalse 3s ease-in;
}
@keyframes revealfalse {
    0% {
        opacity: 0;
        width:40%;
        filter: blur(30px) brightness(0%);
    }
    40% {
        opacity: 100;
        width:90%;
        filter: blur(3px) brightness(30%);
    }
    50% {
        filter: blur(10px) brightness(41%);
    }

    80% {
        opacity: 100;
        width: 90%;
        filter: blur(1px) brightness(37%);
    }

    90% {
        opacity: 100;
        width: 90%;
        filter: blur(0.5px) brightness(50%);
    }
    100% {
        opacity: 0;
        width:30%;
        filter: blur(20px) brightness(0%);
    }
    
}
@keyframes revealtrue {
    0% {
        opacity: 0;
        width:40%;
        filter: blur(30px) brightness(13%);
    }
    40% {
        opacity: 100;
        width:100%;
        filter: blur(2px) brightness(103%);
    }
    60% {
        opacity: 100;
        width:100%;
        filter: blur(0.5px) brightness(113%);
    }

    90% {
        opacity: 100;
        width: 100%;
        filter:blur(0px) brightness(110%);
    }
    99% {
        opacity: 90;
        width:40%;
        filter:blur(30px) brightness(31%);
    }
    100% {
        opacity: 0;
        width:40%;
        filter:blur(30px) brightness(31%);
    }
    
}

.trinity {
    z-index: 2;
    align-self: center;
    grid-area: 2/2;
    width: 300%;
    height: auto;
    opacity: 100%;
  }

.trinityBG {
    z-index: 1;
    align-self: center;
    grid-area: 2/2;
    width: 300%;
    height: auto;
    filter: blur(3px);
  }
  
#glow {
    filter: blur(3px);
    animation-iteration-count: infinite;
    animation-duration: 10s;
    animation-timing-function: ease-in-out;
    animation-name: glow1;
}

@keyframes glow1 {
 0%  { filter: blur(3px) brightness(100%);}
 13%  { filter: blur(15px) brightness(130%);}
 40%  { filter: blur(5px) brightness(90%);}
 58%  { filter: blur(14px) brightness(115%);}
 71%  { filter: blur(3px) brightness(100%);}
 88%  { filter: blur(16px) brightness(120%);}
 100%  { filter: blur(3px) brightness(100%);}
}

.progressBar {
    /* position: relative; */
    margin: auto;
    margin-top: 2vh;
    z-index: 13;
    width: 44vh;
    /* height: 1.5vh; */
    border-radius: 1vh;
    background-color: rgb(255, 235, 235);
    overflow: hidden;
}
.innerProgressBar {
    /* margin: auto; */
    z-index: 14;
    /* position: absolute; */
    /* left: -10vh; */
    height: 1.5vh;
    border-radius: 1vh;
    background-color: rgb(255, 140, 74);
    transition: 1s ease 1s;
}

