.Header h2 {
    text-align: center;  
    z-index: 2;
    padding: 0;
    padding-bottom: 10vh;
    font-size: 5vh;
    margin: 0;
    color: #f6e6d4
  }

  h4 {
    text-align: center;  
    z-index: 2;
    padding: 0;
    padding-top: 4vh;
    font-size: 2.5vh;
    margin: 0;
    color: #f6e6d4
  }

.ResultsText {
    z-index: 1;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* padding-top: 1vh; */
    font-size: 2vh;
    color: #f6e6d4
  }

  .progressDetails {
    width: 45vh;
    margin: auto;
}
.listProgressDetails{
    display: flex;
    justify-content: space-between;
    text-align: center;
    list-style: none;
    margin: 0;
    padding: 0;
    padding-top: .2vh;
    font-size: 1.8vh;
    color: #f6e6d4
}

li {
    display: inline;
}

#fadeIn {
  opacity: 100%;
  transition: opacity 1s;
  }
#fadeOut {
  opacity: 0%;
  transition: opacity 1s;
  }    

.reset {
  cursor: pointer; 
  font-size: 4vh;
  transition: 1s
}
